
* {
	position: relative;
    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
	z-index: 4;
}
#circle-container {
	position: relative;
	width: 70px;
	height: 102.75px;
	overflow: visible;
	--web-view-name: Web 1280 – 1;
	--web-view-id: Web_1280__1;
	--web-scale-on-resize: true;
	--web-enable-deep-linking: true;
}
#circle-green {
	fill: rgba(78,205,196,1);
	
}
.circle-green {
	position: absolute;
	overflow: visible;
	width: 72.75px;
	height: 72.75px;
	left: 0px;
	top: 0px;
}
#circle-white {
	fill: rgba(255,255,255,1);
}
.circle-white {
	position: absolute;
	overflow: visible;
	width: 59.25px;
	height: 59.25px;
	left: 5.5px;
	top: 5px;
}
.name {
	top: 75px;
	position: relative;
	overflow: visible;
	width: 72.75px;
	white-space: nowrap;
	font-family: Bahnschrift;
	font-style: normal;
	font-weight: bold;
	font-size: 14px;
	color: rgba(26,83,92,1);
	
}

.icon{
	z-index: 5;
	position: absolute;
	top: 20px;
	left: 20px;
	width: 29.625px;
	height: 29.625px;
}
