* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	border: none;
}
#card {
	top:0;
    border-radius: 5px;
	position: relative;
	width: 300px;
	height: 150px;
	background-color: rgba(255,255,255,1);
	overflow: hidden;
	font-weight: bold;
	border: solid 0.1px rgba(71,69,69,0.1);
}

#card-title {
	left: 148px;
	top: 20px;
	position: absolute;
	overflow: visible;
	width: 191px;
	white-space: nowrap;
	text-align: left;
	font-family: Bahnschrift;
	font-style: normal;
	font-weight: normal;
	font-size: 24px;
	color: rgba(71,69,69,1);
}


#card-subtitle {
	opacity: 0.7;
	left: 148px;
	top: 48px;
	position: absolute;
	overflow: visible;
	width: 140px;
	white-space: nowrap;
	text-align: left;
	font-family: Bahnschrift;
	font-style: normal;
	font-weight: lighter;
	font-size: 11.25px;
	color: rgba(71,69,69,1);
}
#img {
	position: absolute;
	width: 108px;
	height: 108px;
	left: 20px;
	top: 20px;
	overflow: visible;
}
#price {
	left: 148px;
	top: 98.717px;
	position: absolute;
	overflow: visible;
	width: 95px;
	white-space: nowrap;
	text-align: left;
	font-family: Bahnschrift;
	font-style: normal;
	font-weight: lighter;
	font-size: 24px;
	color: rgba(71,69,69,1);
}
#heart {
	opacity: 0.5;
	filter: invert(0);
	transition-duration: 0.5s;
	
}
#heart:hover {
	opacity: 0.7;
	transition-duration: 0.5s;

	



	
}

.heart {
	overflow: visible;
	position: absolute;
	width: 31.24px;
	height: 25.576px;
	left: 30px;
	top: 30px;
	transform: matrix(1,0,0,1,0,0);
}


