header{
    width: 100%;
    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */

}
.green{
    width: 100%;
    background-color: #50a773;
    position: static;
}
.navbar{
    padding-top: 0.5%;
    padding-bottom: 0.5%;
    padding-left: 6%;
    padding-right: 6%;
    justify-content: space-around;
    background-color: white; 
    border-bottom: 0.1px solid #4ECDC4;
}

.nav-item{
    margin: 0;    
}

.hover-item{
    padding-right: 0.1rem;
}


d.hover-box{
    padding: 0.4rem;
    transition-duration: 0.5s;
    opacity: 1;
}

d.hover-box:hover{
    filter: invert(79%) sepia(74%) saturate(352%) hue-rotate(112deg) brightness(84%) contrast(88%);
    
}

.icon-box{
    width: 300px;
}


input[type=text]:focus {
    box-shadow: none;
    border-color: #ced4da;
    background-color: #F3F3F3;
}

input[type=text]{
    padding: 20px;
    height: 50px;
    width: 25rem;
    border-color: #ced4da;
    background-color: #F3F3F3;
    font-family: Bahnschrift;
	font-style: light;
	font-weight: lighter;
	font-size: 18px;
}

.pin{
    margin-right: 5px;
    text-align: left;
	font-family: Bahnschrift;
	font-style: light;
	font-weight: lighter;
    font-size: 20px;
    
}

.pin:hover{
    fill: black;
}
