body{
    font-family: 'Roboto', sans-serif;
    font-weight: 200;
    }

.content-text{
    width: initial;
}
   
 
 
 .parent{
    position: absolute;

}

.child-one{
    position: fixed;
    width: 100%;
    z-index: 10;
    height: 10vh;
}

.child-two{
    position: static;
    margin-top: 10vh;
    height: 5000vh;
    width: 100vw;
    color: white;
    
} 




     
@media (max-width: 768px) {

    .hover-item{
        font-size: 0;
    }

    .child-one{
        position: fixed;
    }

    .hover-item{
        font-size: 0;
    }

    input[type=text]{
        width: 80vw;
        font: 1rem;
    }
    
    b.logo{
        justify-content: center;
        
    }

    .child-two{
          
    }

    .child-one{
        position: relative;
    }

    .corpo{
        width: 100vw;
    }

    .sticky{
        top: 0px!important;
    }

    .sidenav-container{
        position: absolute;
        left: 0px!important;
        width: 100vw!important;
        z-index: 150!important;
        top: 0px!important;
        display: block!important;
        
    }

    .sidenav-card-container{
        width: 100vw!important;

    }

    #CARRINHO{
        width: 100vw!important;
    }

    
    .MakePizza{
        top: 30px
    }



    

    

   
}

::-webkit-scrollbar {
scrollbar-width: none;
width: 0px;
height: 0px;
margin: 0;
padding: 0;
}

