.sidenav-card-container::-webkit-scrollbar {
	width: 5px;
	height: 5px;
  }
  .sidenav-card-container::-webkit-scrollbar-button {
	width: 0px;
	height: 0px;
  }
  .sidenav-card-container::-webkit-scrollbar-thumb {
	background: #4ecdc4;
	border: 0px none #ffffff;
	border-radius: 19px;
  }
  .sidenav-card-container::-webkit-scrollbar-thumb:hover {
	background: #4ecdc4;
  }
  .sidenav-card-container::-webkit-scrollbar-thumb:active {
	background: #4ecdc4;
  }
  .sidenav-card-container::-webkit-scrollbar-track {
	background: #ffffff;
	border: 0px none #ffffff;
	border-radius: 0px;
  }
  .sidenav-card-container::-webkit-scrollbar-track:hover {
	background: #ffffff;
  }
  .sidenav-card-container::-webkit-scrollbar-track:active {
	background: #ffffff;
  }
  .sidenav-card-container::-webkit-scrollbar-corner {
	background: transparent;
  }


  